






















import { Component, Vue, Watch } from 'vue-property-decorator';

interface Menu {
  title: string;
  url: string;
}

@Component({})
export default class Header extends Vue {
  menus: Menu[] = [
    {
      title: "首页",
      url: "home"
    },
    {
      title: '解决方案',
      url: 'solution'
    },
    {
      title: '关于我们',
      url: 'about'
    },
    {
      title: '加入我们',
      url: 'join'
    }
  ];

  ch: boolean = true;

  activeName: string = 'home';

  @Watch('$route')
  routechange(to: any, from: any) {
    this.activeName = to.name
  }

  changeMenu(name: string) {
    this.activeName = name;
  }
  changeLange() {
    this.ch = !this.ch;
  }
}
